<template>
  <div class="case">
    <mHeader goBack="true" ref="child1" :head-title="$t('birthday.case2Title')"></mHeader>
    <div class="caseTop">
      <caseTop :head-top="$t('birthday.case2Top')"></caseTop>
    </div>
    <div class="caseContent">
      <!--tab-->
      <van-tabs :active="active" class="caseVantTab">
        <van-tab>
          <div slot="title">
            <goodsTitle :goods-title="$t('other.goods')"></goodsTitle>
          </div>

          <div class="tabList">
            <div class="case1">
              <ModuleOne
                :titleDt="$t('birthday.c2titleDt')"
                :titleDd="$t('birthday.c2titleDd')"
                :titleSpan="$t('birthday.c2titleSpan')"
              />
            </div>
            <div class="case2">
              <moduleTwo
                :twoP="$t('birthday.c2twoP')"
                :twoSpan="$t('birthday.c2twoSpan')"
                :twoSpan2="$t('birthday.c2twoSpan2')"
              />
            </div>
            <div class="case3">
              <ModuleThree
                :threeP="$t('birthday.c2threeP')"
                :threeSpan="$t('birthday.c2threeSpan')"
              />
            </div>
          </div>
        </van-tab>


        <van-tab>
          <div slot="title">
            <goodsTitle :goods-title="$t('other.details')"></goodsTitle>
          </div>
          <div class="tabList2">
            <div class="case10">
              <moduleTen3
                :tenDt1="$t('birthday.c2tenDt1')"
                :tenDd1="$t('birthday.c2tenDd1')"
                :tenDt2="$t('birthday.c2tenDt2')"
                :tenDd2="$t('birthday.c2tenDd2')"
                :tenDt3="$t('birthday.c2tenDt3')"
                :tenDd3="$t('birthday.c2tenDd3')"
                :tenDt4="$t('birthday.c2tenDt4')"
                :tenDd4="$t('birthday.c2tenDd4')"
                :tenDt5="$t('birthday.c2tenDt5')"
                :tenDd5="$t('birthday.c2tenDd5')"
                :tenDt6="$t('birthday.c2tenDt6')"
                :tenDd6="$t('birthday.c2tenDd6')"
                :tenDt7="$t('birthday.c2tenDt7')"
                :tenDd7="$t('birthday.c2tenDd7')"
                :tenDt8="$t('birthday.c2tenDt8')"
                :tenDd8="$t('birthday.c2tenDd8')"
              />
            </div>
          </div>
        </van-tab>

      </van-tabs>
    </div>
    <mFooter/>
  </div>
</template>

<script>
  import mHeader from '../../../components/header/headerTab.vue'
  import caseTop from '../../../components/caseTopScreen.vue'
  import goodsTitle from '../../../components/goods.vue'
  import mFooter from '../../../components/footer/footer.vue'

  import ModuleOne from "../../../components/module/moduleOne.vue"
  import ModuleTwo from "../../../components/module/ModuleTwo.vue"
  import ModuleThree from "../../../components/module/ModuleThree.vue"
  import ModuleTen3 from "../../../components/module/ModuleTen3.vue"

  export default {
    name: 'page-tab-container',
    data() {
      return {
        active: 0,
//        listProcess:[{'dd':'JSS特供红色系花卉，打造西班牙式的热情。','dt':'花卉'},{'dd':'专业化妆师定制生日造型，做最美的寿星。','dt':'妆容'},{'dd':'JSS特供粉红色主题花卉，少女感十足。','dt':'花卉'},{'dd':'专业摄影师跟拍，捕捉精彩瞬间。另有摄像师全程记录并剪辑，根据需求制作不同风格VCR。','dt':'记录'},{'dd':'VR游戏、世界百大DJ主持派对、惊喜生日短片。','dt':'娱乐'},{'dd':'邀请函设计、晚宴菜单设计及制作。','dt':'物料设计'}]
      };
    },
    mounted () {
      window.addEventListener('scroll', this.handleScroll)
      this.handleScroll();
    },
    destroyed () {
      // 离开页面移除
      window.removeEventListener('scroll', this.handleScroll)
    },
    methods:{
      handleClick(tab, event) {
        console.log(tab, event);
      },
      handleScroll() {
        let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
        //console.log(scrollTop);
        let opcaity = (scrollTop / 100 > 1) ? 1 : scrollTop / 100;
        this.$refs.child1.headerBac(opcaity);
      }
    },
    components: {
      mHeader,
      mFooter,
      caseTop,
      goodsTitle,
      ModuleOne,
      ModuleTwo,
      ModuleThree,
      ModuleTen3
    }
  }
</script>
<style lang="scss" scoped>
  @import "../../../style/mixin";
  .case{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 102;
    background: #070705;
    .caseTop{
      padding-top: .88rem;
      width: 100%;
    }
    .caseContent{
      background: #fff;
      .caseVantTab{
        height: 100%;
        .tabList{
          padding-top: 2rem;
          .case1{
            margin-bottom: 1rem;
          }
          .case2{
            margin-bottom: 1.2rem;
          }
          .case3{
            margin-bottom: .8rem;
          }
        }
        .tabList2{
          .case10{
            padding-bottom: .8rem;
            padding-top: 1.82rem;
          }
        }
      }
    }
  }

</style>



